* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  /* background-color: white;
  color: black; */
  transition: background-color 0.3s ease, color 0.3s ease;
  display: flex;
  justify-content: center;
  font-family: "inter", sans-serif;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}
:root {
  --bg-color: #f8f8f8;
  --text-color: #050505;
  --accent-color: #9bb537;
  --sidebar-bg-color: #9bb537;
}
.dark-theme {
  --bg-color: #050505;
  --text-color: #f8f8f8;
  --accent-color: #9bb537;
  --sidebar-bg-color: #050505;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

/* Header*/
.header {
  display: flex;
  margin-top: 20px;
}
.header-container-logo {
  display: flex;
  align-items: center;
  gap: 160px;
}
.header-icons {
  display: flex;
  gap: 14px;
  align-items: center;
}

/* Sidebar*/
.sidebar {
  display: none;
  background-color: var(--sidebar-bg-color);
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}

#open-sidebar {
  cursor: pointer;
}
#open-sidebar:hover {
  transform: scale(1.1);
  transition: all 0.3s ease;
}
.close-sidebar {
  position: relative;
  top: -240px;
  left: 131px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.sidebar-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sidebar-list {
  display: flex;
  flex-flow: column;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  color: #fff;
}

.theme-switch-sidebar {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 20px;
  bottom: -217px;
  right: 112px;
}

.theme-switch-sidebar input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  cursor: pointer;
  transition: 0.4s;
}

.slider:before {
  content: "";
  position: absolute;
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.4s ease;
}

input:checked + .slider {
  background-color: #cecdcd;
}

input:checked + .slider:before {
  transform: translateX(26px);
}
.theme-switch,
.header-list {
  display: none;
}

/* Tablet */
@media screen and (min-width: 768px) {
  #open-sidebar {
    display: none;
  }
  .header-list {
    display: flex;
    gap: 15px;
    font-size: 12px;
    color: var(--text-color);
  }
  .header-list a {
    transition: transform 0.4s ease;
  }
  .header-list a:hover {
    color: var(--accent-color);
  }
  .theme-switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 20px;
  }
  .theme-switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    transition: background-color 0.4s ease;
  }

  .slider:before {
    transition: transform 0.4s ease;
  }
  input:checked + .slider {
    background-color: var(--accent-color);
  }
}
@media screen and (min-width: 1280px) {
  .header-list {
    font-size: 14px;
  }
  .header-container-logo {
    gap: 384px;
  }
}
